import searchBox from '@/components/searchTopBox';
import numberBox from '@/components/uni-ui/numberBox/numberBox';
export default {
	components: {
		searchBox,
		numberBox
	},
	data() {
		return {
			priceOrder: 0, //筛选tab  1 价格从低到高 2价格从高到低
			filterIndex: 0, //筛选tab 当前的标签
			cateMaskState: 0, //分类面板展开状态
			cateList:[],//筛选条件 数据
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			flist: [],
			slist: [],
			tlist: [],
		}
	},
	onLoad(){
		this.loadData();
	},
	methods: {
		async loadData(){
			let list = await this.$apiFn.asyncJson('cateList');
			list.forEach(item=>{
				if(!item.pid){
					this.flist.push(item);  //pid为父级id, 没有pid或者pid=0是一级分类
				}else if(!item.picture){
					this.slist.push(item); //没有图的是2级分类
				}else{
					this.tlist.push(item); //3级分类
				}
			}) 
		},
		//一级分类点击
		tabtap(item){
			let _this = this;
			if(!this.sizeCalcState){
				this.calcSize();
			}
			this.currentId = item.id;
			let index = this.slist.findIndex(sitem=>sitem.pid === item.id);
			setTimeout(function(){
				_this.tabScrollTop = _this.slist[index].top;
			},200)
			
		},
		//右侧栏滚动
		asideScroll(e){
			console.log('滚动 ',e)
			if(!this.sizeCalcState){
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
			if(tabs.length > 0){
				this.currentId = tabs[0].pid;
			}
		},
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			console.log('点击',type,this.cateMaskState,state)
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//规格弹窗开关
		toggleSpec(el) {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			let h = 0;
			this.slist.forEach(item=>{
				let view = uni.createSelectorQuery().select("#main-" + item.id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(sid, tid){
			uni.navigateTo({
				url: `/pages/product/product`
			})
		},
		//筛选点击
		tabClick(index){
			console.log('索引',index)
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			// uni.pageScrollTo({
			// 	duration: 300,
			// 	scrollTop: 0
			// })
			// this.loadData('refresh', 1);
			// uni.showLoading({
			// 	title: '正在加载'
			// })
		},
		//数量
		numberChange(data){
			console.log('点击',data)
		},
	}
}
